<template>
  <div class="login" id="login">
    <!-- <Nav /> -->
    <div class="wrap"></div>
    <div class="content">
      <el-button>selfUserId:{{ selfUserId }}</el-button>
      <el-button @click="call">call</el-button>
      <el-button @click="hangUp">hangup</el-button>
      <el-button @click="receive">receive</el-button>
      <el-button @click="refuse">Refuse</el-button>
      <el-button @click="sendRoom"></el-button>
      <el-input
        type="text"
        v-model="userId"
        placeholder=" id"
      />
    </div>

    <!-- <img src="../assets/logo_ico@2x.png" alt="" /> -->
  </div>
</template>

<script>
// @ is an alias to /src
import config from "../config";
import { getSelfUserId } from "../utils/util";
// import Nav from "@/components/navigate.vue";

let { environment, appId, appKey, token } = config;

export default {
  name: "Home",
  data() {
    return {
      id: "",
      isLoading: false,
      userId: "",
      selfUserId: getSelfUserId(),
      personalManager: "",
      liveroomManager: "",
      handleObj: {
        linkv_video_call: this.videoCallHandle,
        linkv_anwser_call: this.anwserCallHandle,
        linkv_hangup_call: this.hangupCallHandle,
      },
      isAudio: null,
      accept: null,
    };
  },
  // components: {
  //   Nav,
  // },
  mounted() {
    let { WebIM } = window.webim;
    window.im = new WebIM({
      environment,
      appId,
      appKey,
    });
    window.im
      .login(String(this.selfUserId), token)
      .then((data) => {
        console.log(data);
        this.$message.success("");
      })
      .catch((err) => {
        console.log(err, "err");
        console.log(err.message);
        this.$message.error("");
      });
    this.personalManager = window.im.personalManager;
    this.liveroomManager = window.im.liveroomManager;
    this.onEvent();
  },
  methods: {
    sendRoom() {
      this.message = "linkv_enable_mic";
      window.im.liveroomManager
        .sendMessage(String(this.selfUserId), this.message)
        .then(
          (data) => {
            this.message = "";
            // this.$message.success("");
            console.log(data);
          },
          (err) => {
            this.$message.success("");
            console.log(err);
          }
        );
    },
    joinIMRoom(value) {
      const { creatRoom } = value;
      const { im } = window;
      // const self = this;
      if (im && this.selfUserId && this.userId) {
        if (creatRoom) {
          im.liveroomManager
            .create(String(this.selfUserId))
            .then(() => {
              this.$message.success("IM");
              // self.joinRtcRoom(true);
            })
            .catch((err) => {
              console.log(err, "");
              this.$message.error(",");
            });
        } else {
          im.liveroomManager
            .join(String(this.userId))
            .then(() => {
              setTimeout(() => {
                // self.joinRtcRoom(false);
              }, 4000);
              this.$message.success("");
            })
            .catch((err) => {
              console.log(err, "");
              this.$message.error(",");
            });
        }
      }
    },
    joinRtcRoom(value) {
      this.$router.push({
        name: "Meet",
        params: {
          source: value ? 1 : 2,
          roomId: value ? this.selfUserId : this.userId,
          userId: this.selfUserId,
        },
      });
    },
    videoCallHandle(content, from) {
      const { isAudio } = content;
      this.isAudio = isAudio;
      this.$message.success(" call ");
      this.userId = from;
      //
    },
    anwserCallHandle(content) {
      const { isAudio, accept } = content;
      this.isAudio = isAudio;
      this.accept = accept;
      this.$message.success("");
      // 
      if (accept) {
        this.joinIMRoom({ createRoom: false });
      } else {
        this.$message.success("");
      }
    },
    hangupCallHandle() {
      this.$message.success("");
    },
    onEvent() {
      let { personalManager, liveroomManager } = this;
      let self = this;
      if (personalManager) {
        personalManager.on("message", (value) => {
          let {
            // eslint-disable-next-line no-unused-vars
            $data: { content, extend1, from },
          } = value;
          content = JSON.parse(content);

          self.handleObj[extend1](content, from);
          // this.$message.success("");
          console.log(value);
        });
      }
      if (liveroomManager) {
        liveroomManager.on("message", (value) => {
          console.log(value);
        });
      }
    },
    // 
    call() {
      const { personalManager, userId } = this;
      if (personalManager && userId) {
        let msg = { isAudio: false, extra: "" };
        let content = JSON.stringify(msg);
        let type = "linkv_video_call";
        personalManager
          .sendEventMessage(String(userId), content, type)
          .then((res) => {
            console.log(res);
            this.$message.success("");
          })
          .catch((err) => {
            console.log("", err);
            this.$message.error("");
          });
      } else {
        this.$message.error("");
      }
    },
    receive() {
      const { personalManager, userId } = this;
      if (personalManager && userId) {
        this.receiveAndRefuse(true);
      }
    },
    refuse() {
      this.receiveAndRefuse(false);
    },
    //
    hangUp() {
      const { personalManager, userId } = this;
      if (personalManager && userId) {
        let msg = { extra: "" };
        let content = JSON.stringify(msg);
        let type = "linkv_hangup_call";
        personalManager
          .sendEventMessage(String(userId), content, type)
          .then((res) => {
            console.log(res);
            this.$message.success("");
          })
          .catch((err) => {
            console.log("", err);
            this.$message.error("");
          });
      } else {
        this.$message.error("");
      }
    },
    // 
    receiveAndRefuse(accept) {
      const { personalManager, userId } = this;
      if (personalManager && userId) {
        let msg = { isAudio: false, extra: "", accept };
        let content = JSON.stringify(msg);
        let type = "linkv_anwser_call";
        personalManager
          .sendEventMessage(String(userId), content, type)
          .then((res) => {
            console.log(res);
            console.log("");
            this.joinIMRoom({ creatRoom: true });
          })
          .catch((err) => {
            console.log("", err);
            this.$message.error("");
          });
      } else {
        this.$message.error("");
      }
    },
  },
};
</script>

<style lang="less">
.login {
  .content {
    margin-top: 200px;
  }
  .el-button {
    font-size: 20px;
  }
}
</style>
